import React, { useEffect } from "react";
import { RecipeFoodCard, RecipeFoodRow } from "../compontents";
import { SelectBar } from "../compontents/SelectBar";
import {
  ALL_NUTRIENTS,
  highCholesterol,
  ALL_FOOD_LIST,
  ALL_FOOD_CATEGORY_LIST,
  getFoodList,
} from "../constants/mock-food";
import { FoodItemFilter } from "../compontents/FoodItemFilter";
import {
  Container,
  FormControlLabel,
  FormGroup,
  Typography,
  Switch,
} from "@material-ui/core";
import { FOOD_LIST } from "../constants/food-list";

export const RecipeFoodPage = ({
  showAllFood,
  setShowAllFood,
  filterOption,
  setFilterOption,
}) => {
  const RECOMMENDED_NUTRIENTS = ALL_NUTRIENTS.filter((nutrient) =>
    highCholesterol.some((name) => name === nutrient.name)
  );

  const getAllFilteredItems = (name, type, itemsCount) => {
    const currentFoodList = showAllFood ? ALL_FOOD_LIST : FOOD_LIST;

    const sortedItems = currentFoodList
      .sort(
        (currentFood, nextFood) =>
          nextFood.nutrition[type][name] - currentFood.nutrition[type][name]
      )
      .slice(0, itemsCount);

    return sortedItems;
  };

  //! make them as separate files/react page components
  //! add rohlik id?
  const recommendedPage = () => {
    return (
      <div>
        <FormControlLabel
          style={{ marginLeft: 20 }}
          control={
            <Switch
              onClick={() => setShowAllFood(!showAllFood)}
              checked={!showAllFood}
            />
          }
          label="Minimalizace Potravin"
        />

        {RECOMMENDED_NUTRIENTS.map(({ title, name, type, isMacro }) => {
          return (
            <RecipeFoodRow key={name} title={title}>
              {getAllFilteredItems(isMacro ? "total" : name, type, 10).map(
                (food) => {
                  const {
                    id,
                    title,
                    imgSrc,
                    nutrition,
                    grammageInfo,
                    price,
                    currentVariant,
                    category,
                    variants,
                  } = food;

                  const nutrientValue =
                    food.nutrition[type][isMacro ? "total" : name];

                  let nutrientUnit;

                  ALL_NUTRIENTS.forEach((nutrient) => {
                    if (nutrient.name === name) {
                      return (nutrientUnit = nutrient.unit);
                    }
                  });

                  return (
                    <RecipeFoodCard
                      foodId={id}
                      title={title}
                      imgSrc={imgSrc}
                      nutrition={nutrition}
                      grammageInfo={grammageInfo}
                      price={price}
                      currentVariant={currentVariant}
                      category={category}
                      nutrientValue={nutrientValue}
                      nutrientUnit={nutrientUnit}
                      food={food}
                      variants={variants}
                      isMacro={isMacro}
                      nutrientType={type}
                      nutrientName={name}
                    />
                  );
                }
              )}
            </RecipeFoodRow>
          );
        })}
      </div>
    );
  };

  const allItems = () => {
    return (
      <div>
        {Object.keys(getFoodList(showAllFood)).map((key, index) => {
          const currentItem = getFoodList(showAllFood)[key];

          return (
            <div style={{ margin: "15px 0" }}>
              <Typography
                // color='secondary'
                variant="h4"
                component="h3"
                className="create-recipe__title"
              >
                {currentItem.name}
              </Typography>
              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  justifyContent: "flex-start",
                }}
              >
                {currentItem.items
                  .sort((a, b) =>
                    a.title > b.title ? 1 : b.title > a.title ? -1 : 0
                  )
                  .map((food) => {
                    const {
                      id,
                      title,
                      imgSrc,
                      nutrition,
                      grammageInfo,
                      price,
                      currentVariant,
                      category,
                      variants,
                    } = food;

                    return (
                      <RecipeFoodCard
                        key={id}
                        foodId={id}
                        title={title}
                        imgSrc={imgSrc}
                        nutrition={nutrition}
                        grammageInfo={grammageInfo}
                        variants={variants}
                        price={price}
                        category={category}
                        small
                        currentVariant={currentVariant}
                        food={food}
                      />
                    );
                  })}
              </div>
            </div>
          );
        })}
      </div>
    );
  };
  // ALL_FOOD_LIST.map((food) => {
  //   const {
  //     id,
  //     title,
  //     imgSrc,
  //     nutrition,
  //     grammageInfo,
  //     price,
  //     currentVariant,
  //     category,
  //   } = food;

  //   return (
  //     <RecipeFoodCard
  //       key={id}
  //       foodId={id}
  //       title={title}
  //       imgSrc={imgSrc}
  //       nutrition={nutrition}
  //       grammageInfo={grammageInfo}
  //       price={price}
  //       category={category}
  //       small
  //       currentVariant={currentVariant}
  //     />
  //   );
  // });

  return (
    <div className="food-page">
      <div className="food-page__header">
        {/* <SelectBar /> */}

        <FoodItemFilter
          filterOption={filterOption}
          setFilterOption={setFilterOption}
        />
      </div>
      <div
        className="food-page__content"
        style={{ display: "flex", justifyContent: "flex-start" }}
      >
        <Container>
          {filterOption === "recommended" ? recommendedPage() : allItems()}
        </Container>
      </div>
    </div>
  );
};
