import React, { useState, useEffect } from "react";
import { useFoodStateValue } from "../context/FoodStateContext";
import { useFoodStateHelpers } from "../hooks/useFoodStateHelpers.js";
import { makeStyles } from "@material-ui/core/styles";
import { v4 as uuidv4 } from "uuid";

import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  Input,
  FormControl,
  TextField,
  Select,
  InputAdornment,
} from "@material-ui/core";
import clsx from "clsx";
import { FOOD_LIST } from "../constants/food-list";
import { SEASONING_LIST } from "../constants/mock-seasoning";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexWrap: "wrap",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  marginTop: {
    marginTop: "2rem",
  },
}));

export function GrammageModal({
  foodVariant,
  title,
  variants,
  currentUnit,
  grammageInfo,
  itemId,
  children,
  foodItem,
}) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [grammage, setGrammage] = useState("");

  const { foodState, setFoodState } = useFoodStateValue();
  const { getAvailableFood, getAvailableFoodIndex } =
    useFoodStateHelpers(title);
  const [grammageUnit, setGrammageUnit] = useState(grammageInfo.defaultUnit);

  const getGrammageStateValue = () => {
    return getAvailableFood(itemId).grammage.value;
  };

  const handleClose = () => {
    setOpen(false);
  };

  const convertUnitValue = (unit, value, isMain = false) => {
    let convertedValue = Number(value);

    switch (unit) {
      case "kg":
        convertedValue = value * 1000;
        break;
      case "l":
        convertedValue = value * 1000;
        break;
      case "customUnit0":
        convertedValue = value * grammageInfo.customUnit[0].value;
        break;
      case "customUnit1":
        convertedValue = value * grammageInfo.customUnit[1].value;
        break;
      case "customUnit2":
        convertedValue = value * grammageInfo.customUnit[2].value;
        break;
      case "cup":
        convertedValue = value * 250;
        break;
      case "smallCup":
        convertedValue = value * 200;
        break;
      case "tbs":
        convertedValue = value * 15;
        break;
      case "ts":
        convertedValue = value * 5;
        break;
      case "pinch":
        convertedValue = value * 0.35;
        break;
      default:
    }

    if (isMain) return convertedValue / 1000;

    return convertedValue;
  };

  const getPlaceholderUnit = (unit) => {
    if (unit === "g" || unit === "ml" || unit === "kg" || unit === "l")
      return unit;
    return "x";
  };

  const setGrammageState = () => {
    setFoodState((prevState) => {
      return prevState.map((food, index) => {
        if (food.itemId === itemId) {
          return {
            ...prevState[index],
            nutrition: foodItem.nutrition,
            selectedVariant: foodVariant,
            grammage: {
              ...prevState[index].grammage,
              value: convertUnitValue(grammageUnit, grammage),
              valueInMainUnit: convertUnitValue(grammageUnit, grammage, true),
              valueInDefaultUnit: convertUnitValue(grammageUnit, grammage),
              //! remove it from the structure => defaultUnit in grammageInfo
              unit: foodItem.grammageInfo.defaultUnit,
            },
            grammageInfo,
            price: foodItem.price,
            imgSrc: foodItem.imgSrc,
          };
        } else {
          return food;
        }
      });
    });

    handleClose();
  };

  useEffect(() => {
    getGrammageStateValue() !== 0 && setGrammage(getGrammageStateValue());

    setGrammageUnit(currentUnit);
  }, []);

  const handleDuplicateFood = () => {
    const newFoodState = [...foodState];

    const foodIndex = foodState.findIndex((food) => food.title === title);

    const newFood = {
      ...foodState[foodIndex],
      itemId: uuidv4(),
    };

    newFoodState.push(newFood);

    setFoodState(newFoodState);
  };

  return (
    <div className="grammage-modal">
      <div onClick={() => setOpen(true)}>{children}</div>

      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <DialogTitle>{title}</DialogTitle>
          {variants.length > 0 && (
            <Button
              style={{ marginRight: "16px" }}
              color="primary"
              onClick={handleDuplicateFood}
            >
              + Nová Varianta
            </Button>
          )}
        </div>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor="demo-dialog-native">Jednotka</InputLabel>
              <Select
                native
                value={grammageUnit}
                onChange={(e) => {
                  setGrammage("");
                  setGrammageUnit(e.target.value || "");
                }}
                input={<Input id="demo-dialog-native" />}
              >
                {foodItem.grammageInfo.type === "foodstuff" ||
                foodItem.grammageInfo.type === "ingredient" ? (
                  <>
                    <option value="kg">Kilogramy</option>
                    <option value="g">Gramy</option>
                    {foodItem.grammageInfo.customUnit &&
                      foodItem.grammageInfo.customUnit.map(
                        ({ name, value }, i) => (
                          <option value={`customUnit${i}`}>
                            {name} ({value} g)
                          </option>
                        )
                      )}
                  </>
                ) : foodItem.grammageInfo.type === "liquid" ||
                  foodItem.grammageInfo.type === "sauce" ? (
                  <>
                    <option value="l">Litry</option>
                    <option value="ml">Mililitry</option>
                    <option value="cup">Sklenice (250ml)</option>
                    <option value="smallCup">Malá Sklenice (200ml)</option>
                    <option value="tbs">Lžíce (15ml)</option>
                    <option value="ts">Lžička (5ml)</option>
                  </>
                ) : foodItem.grammageInfo.type === "seasoning" ? (
                  <>
                    <option value="g">Gramy</option>
                    <option value="pinch">Špetka (0.35g)</option>
                    <option value="tbs">Lžíce (15g)</option>
                    <option value="ts">Lžička (5g)</option>
                  </>
                ) : (
                  ""
                )}
              </Select>
            </FormControl>

            <TextField
              type="number"
              onInput={(e) => {
                setGrammage(e.target.value);
              }}
              label="Zadejte Hmotnost"
              id="outlined-start-adornment"
              className={clsx(classes.marginTop, classes.textField)}
              InputProps={{
                startAdornment: (
                  //!redo the unit system
                  //!milk shows as 1000g instead od ml
                  <InputAdornment position="start">
                    {getPlaceholderUnit(grammageUnit)}
                  </InputAdornment>
                ),
              }}
              value={grammage}
              variant="outlined"
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Zrušit
          </Button>
          <Button
            onClick={() => {
              setGrammageState();
            }}
            color="primary"
          >
            Potvrdit
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
