import React, { useState } from "react";
import {
  AppBar,
  Box,
  Button,
  Icon,
  IconButton,
  Modal,
  SwipeableDrawer,
  Toolbar,
  Tooltip,
  Typography,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { MenuBasket } from "../MenuBasket";
import cholifyLogo from "../../img/cholify-web-logo.svg";
import { MenuItemsListModal } from "../MenuItemsListModal";
import { LottieAnimation } from "../LottieAnimation";
import { useFoodStateHelpers } from "../../hooks/useFoodStateHelpers";
import CloseIcon from "@material-ui/icons/Close";
import SearchIcon from "@material-ui/icons/Search";
import SettingsIcon from "@material-ui/icons/Settings";

import { MenuItemList } from "../MenuItemList";
import { useMenuStateValue } from "../../context/MenuStateContext";
import { List } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { SearchModal } from "../SearchModal";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  listItem: {
    paddingLeft: 0,
    paddingRight: 0,
  },
  modalStyle: {
    minWidth: "75%",
    minHeight: "26rem",
  },
  drawer: {
    width: () =>
      window.screen.width < 450 ? `${window.screen.width}px` : "360px",
  },
}));
console.log(window.screen.width);

export const Header = ({ passRef }) => {
  const { isFoodStateEmpty } = useFoodStateHelpers();
  const { menuState } = useMenuStateValue();
  const classes = useStyles();

  const [drawerOpened, setDrawerOpened] = useState(false);

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  // console.log('isFoodStateEmpty', isFoodStateEmpty);

  const toggleDrawer = (open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setDrawerOpened(open);
  };

  return (
    <AppBar ref={passRef} className="header" position="absolute">
      <Toolbar className="header__toolbar">
        <img
          className="header__logo"
          src={cholifyLogo}
          alt="Cholify header logo"
        />
        <div>
          <Tooltip title="Vyhledávání">
            <IconButton
              onClick={() => {
                setDrawerOpened(false);
                handleOpen();
              }}
              aria-label="Search Button"
            >
              <SearchIcon />
            </IconButton>
          </Tooltip>
          <Modal open={open} onClose={handleClose}>
            <SearchModal handleClose={handleClose} />
          </Modal>
          <>
            <MenuBasket
              menuItemCount={menuState.length}
              setDrawerOpened={setDrawerOpened}
              drawerOpened={drawerOpened}
            />
            <SwipeableDrawer
              anchor="right"
              open={drawerOpened}
              onClose={toggleDrawer(false)}
              onOpen={toggleDrawer(true)}
              paper
            >
              <Box className={classes.drawer} role="presentation">
                <div style={{ padding: 25, height: "100vh" }}>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                      marginBottom: 20,
                    }}
                  >
                    <Typography variant="h4" style={{ fontWeight: 500 }}>
                      Moje Menu
                    </Typography>
                    <Tooltip title="Zavřít Menu">
                      <IconButton
                        onClick={() => {
                          setDrawerOpened(false);
                        }}
                        aria-label="Close Menu"
                      >
                        <CloseIcon />
                      </IconButton>
                    </Tooltip>
                  </div>

                  {menuState.length === 0 ? (
                    <div
                      style={{
                        textAlign: "center",
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignItems: "center",
                        marginTop: 80,
                      }}
                    >
                      <LottieAnimation url="./images/animations/cooking-process.json" />
                      <Typography variant="h5" style={{ fontWeight: 500 }}>
                        PŘIDEJTE RECEPT
                      </Typography>
                    </div>
                  ) : (
                    <List className={classes.root}>
                      {menuState.map((recipe) => (
                        <MenuItemList recipe={recipe} />
                      ))}
                    </List>
                  )}
                </div>
              </Box>
            </SwipeableDrawer>
          </>
          <Tooltip title="Nastavení">
            <IconButton
              style={{ marginLeft: 2 }}
              onClick={() => {
                setDrawerOpened(false);
              }}
              aria-label="Settings Button"
            >
              <SettingsIcon />
            </IconButton>
          </Tooltip>
        </div>
      </Toolbar>
    </AppBar>
  );
};
