import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
  Tooltip,
} from "@material-ui/core";
import { GrammageModal } from "./GrammageModal";
import { useManageFoodState } from "../hooks/useManageFoodState.js.js";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import TuneIcon from "@material-ui/icons/Tune";

const useStyles = makeStyles((theme) => ({
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: 151,
    backgroundSize: "contain",
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  marginRight: {
    marginRight: "0.5rem",
  },
}));

export function RecipeCard({ foodItem }) {
  const [foodVariant, setFoodVariant] = useState(0);

  const classes = useStyles();
  const { removeItemFromState } = useManageFoodState();

  const {
    foodId,
    itemId,
    title,
    grammage,
    grammageInfo,
    imgSrc,
    variants,
    category,
    currentVariant,
  } = foodItem;

  const getPlaceholderUnit = (unit) => {
    if (unit === "bigPiece") return `x ${grammageInfo.piece.bigPiece}g`;
    if (unit === "smallPiece") return `x ${grammageInfo.piece.smallPiece}g`;
    if (unit === "bigPackage")
      return `x ${grammageInfo.package.bigPackage}${grammageInfo.defaultUnit}`;
    if (unit === "smallPackage")
      return `x ${grammageInfo.package.smallPackage}${grammageInfo.defaultUnit}`;
    if (unit === "customUnit0")
      return `${grammageInfo.customUnit[0].name} (${grammageInfo.customUnit[0].value} g)`;
    if (unit === "customUnit1")
      return `${grammageInfo.customUnit[1].name} (${grammageInfo.customUnit[1].value} g)`;
    if (unit === "customUnit2")
      return `${grammageInfo.customUnit[2].name} (${grammageInfo.customUnit[2].value} g)`;
    if (unit === "cup") return "x 250ml";
    if (unit === "smallCup") return "x 200ml";
    if (unit === "tbs") return "x 15ml";
    if (unit === "ts") return "x 5ml";
    if (unit === "pinch") return "x 0.35g";
    return unit;
  };

  return (
    <Card className="recipe-card">
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Typography component="h6" variant="h6">
            {title}
          </Typography>
          <Typography
            className="grammage-text"
            component="h6"
            variant="h6"
            color="textSecondary"
          >
            {currentVariant && `(${currentVariant})`}
          </Typography>
          <Typography
            className="grammage-text"
            component="h6"
            variant="h6"
            color="textSecondary"
          >
            {`${grammage.value} ${getPlaceholderUnit(grammage.unit)}`}
          </Typography>
          {/* <Typography variant="subtitle1" color="textSecondary">
            {category}
          </Typography> */}
        </CardContent>
        <div className={classes.controls}>
          <GrammageModal
            foodVariant={foodVariant}
            title={title}
            variants={variants}
            grammageInfo={grammageInfo}
            currentUnit={grammage.unit}
            foodId={foodId}
            itemId={itemId}
            isSeasoning={false}
            foodItem={foodItem}
          >
            <Tooltip title="Upravit Gramáž">
              <Button
                className={classes.marginRight}
                variant="contained"
                color="primary"
              >
                <TuneIcon />
              </Button>
            </Tooltip>
          </GrammageModal>
          <Tooltip title="Odstranit Z Receptu">
            <Button
              variant="outlined"
              color="primary"
              onClick={() => removeItemFromState(title, itemId)}
            >
              <DeleteForeverIcon />
            </Button>
          </Tooltip>
        </div>
      </div>
      <CardMedia className={classes.cover} image={imgSrc} title="" />
    </Card>
  );
}
