export const calculateFoodGrammage = (value, unit, grammageInfo) => {
  if (unit === "g" || unit === "ml") return value;
  if (unit === "kg" || unit === "l") return value * 1000;
  if (unit === "bigPiece") return value * grammageInfo.piece.bigPiece;
  if (unit === "smallPiece" || unit === "l")
    return value * grammageInfo.piece.smallPiece;
  if (unit === "bigPackage") return value * grammageInfo.package.bigPackage;
  if (unit === "smallPackage" || unit === "l")
    return value * grammageInfo.package.smallPackage;
  if (unit === "customUnit0") return value * grammageInfo.customUnit[0].value;
  if (unit === "customUnit1") return value * grammageInfo.customUnit[1].value;
  if (unit === "customUnit2") return value * grammageInfo.customUnit[2].value;
  if (unit === "cup") return value * 250;
  if (unit === "smallCup") return value * 200;
  if (unit === "tbs") return value * 15;
  if (unit === "ts") return value * 5;
  if (unit === "pinch") return value * 0.35;
};
