import React, { createContext, useContext, useState } from "react";

export const ModesContext = createContext();

export const ModesProvider = ({ children }) => {
  const [showAllFood, setShowAllFood] = useState();

  return (
    <ModesContext.Provider value={{ showAllFood, setShowAllFood }}>
      {children}
    </ModesContext.Provider>
  );
};

export const useModesValue = () => useContext(ModesContext);
