import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { Zoom, Fab, Tooltip } from "@material-ui/core";
import { useFoodStateHelpers } from "../hooks/useFoodStateHelpers";

const useStyles = makeStyles((theme) => ({
  fab: {
    position: "fixed",
    bottom: theme.spacing(13),
    right: theme.spacing(4),
  },
  fabSecondary: {
    position: "fixed",
    backgroundColor: "#fff",
    color: "#ec575f",
    bottom: theme.spacing(13),
    right: theme.spacing(13),
  },
}));

export const Fabs = ({
  fab,
  value,
  index,
  onClick,
  isLoading,
  icon,
  isAddFood,
  isSecondary,
  tooltipText,
}) => {
  const theme = useTheme();
  const classes = useStyles();
  const { isFoodStateEmpty } = useFoodStateHelpers();

  const transitionDuration = {
    enter: theme.transitions.duration.enteringScreen,
    exit: theme.transitions.duration.leavingScreen,
  };

  return (
    <Zoom
      key={index}
      in={value === index}
      timeout={transitionDuration}
      style={{
        transitionDelay: `${value === index ? transitionDuration.exit : 0}ms`,
      }}
      unmountOnExit
    >
      <Tooltip title={tooltipText}>
        <Fab
          disabled={isAddFood ? isFoodStateEmpty || isLoading : isLoading}
          onClick={() => {
            onClick();
          }}
          aria-label={fab.label}
          className={isSecondary ? classes.fabSecondary : classes.fab}
          color={isSecondary ? undefined : fab.color}
        >
          {icon}
        </Fab>
      </Tooltip>
    </Zoom>
  );
};
