import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FixedSizeList } from 'react-window';
import { useFoodStateValue } from '../../context/FoodStateContext';
import { useMenuStateValue } from '../../context/MenuStateContext';
import { useMenuItemsList } from '../../hooks/useMenuItemsList';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300,
    padding: '0 15px',
  },
  listSection: {
    backgroundColor: 'inherit',
    margin: '15px 0',
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0,
  },
}));

export const Summary100 = ({ name, nutritionType, unit, isMenu }) => {
  const { foodState } = useFoodStateValue();
  const { menuState } = useMenuStateValue();
  const { calculateMenuItemsValue } = useMenuItemsList();
  // const { calculateMenuItemsValue } = useMenuItemsList();

  const classes = useStyles();

  const renderMenuData = () => {
    const content = menuState.map((menuItem, i) => {
      return (
        <li key={`section-${i}`} className={classes.listSection}>
          <ul className={classes.ul}>
            <ListSubheader style={{ padding: 0 }}>
              {menuItem.name}
            </ListSubheader>
          </ul>

          {menuItem.itemsList
            .sort(
              (a, b) =>
                b.nutrition[nutritionType][name] -
                a.nutrition[nutritionType][name]
            )
            .map((foodItem) => {
              return (
                <ListItem
                  key={foodItem.title}
                  style={{
                    display: 'flex',
                    padding: '10px 0',
                    borderTop: '1px solid gainsboro',
                  }}
                >
                  <ListItemText primary={foodItem.title} style={{ flex: 1 }} />
                  <ListItemText
                    primary={`${foodItem.nutrition[nutritionType][name]} ${unit}`}
                    style={{ flex: 1, textAlign: 'right' }}
                  />
                </ListItem>
              );
            })}
        </li>
      );
    });

    return content;
  };

  return (
    <div className='summary' style={{ marginTop: '20px' }}>
      <div className='summary__head'>
        <h3>Shrnutí nutrientu podle jednotlivých potravin na 100g</h3>
      </div>
      <List className={classes.root} subheader={<li />}>
        {isMenu ? renderMenuData() : ''}
        {/* </FixedSizeList> */}
      </List>
    </div>
  );
};
