import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import {
  AuthProvider,
  ResponseStateProvider,
  FoodStateProvider,
  MenuStateProvider,
  RecipesProvider,
  ModesProvider,
} from "./context";
import { UserDataProvider } from "./context/UserDataContext";
import { BrowserRouter as Router } from "react-router-dom";

import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";

const theme = createMuiTheme({
  palette: {
    primary: {
      // main: '#43a047',
      main: "#EC575F",
    },
    secondary: {
      main: "#FA811F",
    },
  },
  typography: {
    fontFamily: ["Saira", "sans-serif"].join(","),
  },
});

ReactDOM.render(
  <React.StrictMode>
    <ResponseStateProvider>
      <ModesProvider>
        <AuthProvider>
          <UserDataProvider>
            <RecipesProvider>
              <MenuStateProvider>
                <FoodStateProvider>
                  <ThemeProvider theme={theme}>
                    <Router>
                      <App />
                    </Router>
                  </ThemeProvider>
                </FoodStateProvider>
              </MenuStateProvider>
            </RecipesProvider>
          </UserDataProvider>
        </AuthProvider>
      </ModesProvider>
    </ResponseStateProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
