import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";
import { useFoodStateValue } from "../context/FoodStateContext";
import { GrammageModal } from "../compontents/GrammageModal";
import { useFoodStateHelpers } from "../hooks";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(0.5),
    },
    whiteSpace: "nowrap",
    height: "40px",
    display: "flex",
    overflowY: "scroll",
  },
}));

export const Chips = () => {
  const classes = useStyles();
  const { foodState } = useFoodStateValue();
  const { isFoodAvailable } = useFoodStateHelpers();

  const handleClick = () => {
    console.info("You?");
  };

  return (
    <div className={classes.chipsContainer}>
      <div className={classes.root}>
        {foodState
          .filter((item) => item.category === "seasoning")
          .map((food) => {
            const {
              title,
              grammageInfo,
              grammage,
              variants,
              foodId,
              itemId,
              currentVariant,
            } = food;

            const getPlaceholderUnit = (unit) => {
              if (unit === "cup") return "x 250ml";
              if (unit === "smallCup") return "x 200ml";
              if (unit === "tbs") return "x 15ml";
              if (unit === "ts") return "x 5ml";
              return unit;
            };

            return (
              <GrammageModal
                foodVariant={currentVariant}
                title={title}
                variants={variants}
                currentUnit={grammage.unit}
                grammageInfo={grammageInfo}
                itemId={itemId}
                foodItem={food}
              >
                <Chip
                  className={`chip chip--${food.grammageInfo.type} ${
                    grammage.value === 0 ? "" : "chip--active"
                  } `}
                  label={food.title}
                  onClick={handleClick}
                  clickable
                  variant={grammage.value === 0 ? "outlined" : undefined}
                />
              </GrammageModal>
              //! color scheme light for chips when greyed out => maybe 3 colors for differect category?
              //! add an icon when greyed out
            );
          })}
      </div>
    </div>
  );
};
