import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FixedSizeList } from 'react-window';
import { useFoodStateValue } from '../../context/FoodStateContext';
import { useMenuStateValue } from '../../context/MenuStateContext';
import { useMenuItemsList } from '../../hooks/useMenuItemsList';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import { useFoodStateHelpers } from '../../hooks';
import { calculateFoodGrammage } from '../../helpers/calculateFoodGrammage';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300,
    padding: '0 15px',
  },
  listSection: {
    backgroundColor: 'inherit',
    margin: '15px 0',
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0,
  },
}));

export const Summary = ({ name, nutritionType, unit, isMenu }) => {
  const { foodState } = useFoodStateValue();
  const { menuState } = useMenuStateValue();
  const { calculateMenuItemsValue } = useMenuItemsList();
  const { calculateFoodValue } = useFoodStateHelpers();
  // const { calculateMenuItemsValue } = useMenuItemsList();

  const classes = useStyles();

  // console.log(object);

  const calculateCurrentValue = (foodItem) => {
    const grammageMultiplier =
      calculateFoodGrammage(
        foodItem.grammage.value,
        foodItem.grammage.unit,
        foodItem.grammageInfo
      ) / 100;

    const nutrientValue = foodItem.nutrition[nutritionType][name];

    const currentValue = nutrientValue * grammageMultiplier;

    return currentValue.toFixed(1);
  };

  const renderMenuData = () => {
    let content;

    content = menuState.map((menuItem, i) => {
      return (
        <li key={`section-${i}`} className={classes.listSection}>
          <ul className={classes.ul}>
            <ListSubheader style={{ padding: 0 }}>
              {menuItem.name}{' '}
              {isMenu
                ? `(${calculateFoodValue(
                    menuItem.itemsList,
                    name,
                    nutritionType
                  )} ${unit})`
                : ''}
            </ListSubheader>
          </ul>

          {menuItem.itemsList
            .sort((a, b) => {
              return calculateCurrentValue(b) - calculateCurrentValue(a);
            })
            .map((foodItem) => {
              const currentValue = calculateCurrentValue(foodItem);

              return (
                <ListItem
                  key={foodItem.title}
                  style={{
                    display: 'flex',
                    padding: '10px 0',
                    borderTop: '1px solid gainsboro',
                  }}
                >
                  <ListItemText primary={foodItem.title} style={{ flex: 1 }} />
                  <ListItemText
                    primary={`${currentValue} ${unit}`}
                    style={{ flex: 1, textAlign: 'right' }}
                  />
                </ListItem>
              );
            })}
        </li>
      );
    });

    if (menuState.length === 0) {
      content = <h3>Přidejte Recept Do Menu</h3>;
    }

    return content;
  };

  return (
    <div className='summary' style={{ marginTop: '20px' }}>
      <div className='summary__head'>
        <h3>Shrnutí nutrientu podle jednotlivých potravin</h3>
      </div>
      <List className={classes.root} subheader={<li />}>
        {isMenu ? renderMenuData() : ''}
        {/* </FixedSizeList> */}
      </List>
    </div>
  );
};
