//! restructure into an object with property of the specific nutritional values intake

import {
  MACRO_SECTION_DATA,
  VITAMINS_SECTION_DATA,
  MINERALS_SECTION_DATA,
} from "./nutrients";

import { FOOD_LIST } from "./food-list.js";

//! take out the protein and do a research on what nutrients are essential
export const highCholesterol = [
  "protein",
  "fiber",
  "omega3FattyAcids",
  "omega6FattyAcids",
  "niacin",
  "vitaminB12",
  "vitaminC",
  "vitaminD",
  "vitaminE",
  "vitaminK",
  "calcium",
  "potassium",
  "iron",
  "magnesium",
];

export let ALL_NUTRIENTS = [];

MACRO_SECTION_DATA.forEach((item) => {
  ALL_NUTRIENTS.push({ ...item, type: item.name });
  if (item.subSection.subNutrients) {
    item.subSection.subNutrients.forEach((subItem) =>
      ALL_NUTRIENTS.push({ ...subItem, type: item.subSection.section })
    );
  }
});

VITAMINS_SECTION_DATA.forEach((item) => {
  ALL_NUTRIENTS.push({ ...item, type: "vitamins" });
});

MINERALS_SECTION_DATA.forEach((item) => {
  ALL_NUTRIENTS.push({ ...item, type: "minerals" });
});

export let SEARCHBAR_FOOD_LIST = [];
export let ALL_FOOD_LIST = [];

console.log("SEARCHBAR_FOOD_LIST", SEARCHBAR_FOOD_LIST);

FOOD_LIST.forEach((food) => {
  SEARCHBAR_FOOD_LIST.push({
    title: food.title,
    id: food.id,
    imgSrc: food.imgSrc,
  });

  if (food.variants.length > 0) {
    food.variants.forEach((variant, i) => {
      const index = i;

      if (i === 0) {
        i = "";
      }

      ALL_FOOD_LIST.push({
        title: food.title,
        category: food.category,
        currentVariant: food.variants[index],
        rohlikId: food[`rohlikId${i}`],
        nutrition: food[`nutrition${i}`],
        grammageInfo: food[`grammageInfo${i}`],
        price: food[`price${i}`],
        imgSrc: food[`imgSrc${i}`],
      });
    });
  } else {
    ALL_FOOD_LIST.push(food);
  }
});

ALL_FOOD_LIST.map((food, i) => {
  food.id = i;
});

//! could have nested categories

export const getFoodList = () => {
  const ALL_FOOD_CATEGORY_LIST = {
    vegetables: { name: "Zeleniny", items: [] },
    fruits: { name: "Ovoce", items: [] },
    legumes: { name: "Luštěniny", items: [] },
    grains: { name: "Obilniny", items: [] },
    nuts: { name: "Ořechy", items: [] },
    seeds: { name: "Semínka", items: [] },
    meat: { name: "Maso", items: [] },
    fish: { name: "Ryby", items: [] },
    seafood: { name: "Mořské Plody", items: [] },
    dairy: { name: "Mléčné Produkty", items: [] },
    alternatives: { name: "Alternativa", items: [] },
  };

  const {
    vegetables,
    fruits,
    legumes,
    grains,
    nuts,
    seeds,
    meat,
    fish,
    seafood,
    dairy,
    alternatives,
  } = ALL_FOOD_CATEGORY_LIST;

  FOOD_LIST.forEach((food) => {
    switch (food.category) {
      case "vegetables":
        vegetables.items.push(food);
        break;
      case "fruits":
        fruits.items.push(food);
        break;
      case "legumes":
        legumes.items.push(food);
        break;
      case "grains":
        grains.items.push(food);
        break;
      case "seeds":
        seeds.items.push(food);
        break;
      case "nuts":
        nuts.items.push(food);
        break;
      case "meat":
        meat.items.push(food);
        break;
      case "fish":
        fish.items.push(food);
        break;
      case "seafood":
        seafood.items.push(food);
        break;
      case "dairy":
        dairy.items.push(food);
        break;
      case "alternatives":
        alternatives.items.push(food);
        break;

      default:
        break;
    }
  });
  // console.log(ALL_FOOD_CATEGORY_LIST);

  return ALL_FOOD_CATEGORY_LIST;
};

