import {
  Box,
  IconButton,
  makeStyles,
  styled,
  Tooltip,
} from "@material-ui/core";
import { GroupRounded } from "@material-ui/icons";
import { useAutocomplete } from "@material-ui/lab";
import React from "react";
import { FOOD_LIST } from "../constants/food-list";
import CloseIcon from "@material-ui/icons/Close";

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: () =>
      window.screen.width < 450 ? `${window.screen.width}px` : "45%",
    height: () =>
      window.screen.width < 450 ? `${window.screen.height}px` : "80%",
    backgroundColor: "white",
    border: "2px solid #e2e2e2",
    boxShadow: 24,
    padding: 20,
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "flex-start",
    alignItems: "flex-start",
    overflow: "scroll",
    outline: "none",
  },
}));

const Listbox = styled("ul")(({ theme }) => ({
  width: 200,
  margin: 0,
  padding: 0,
  zIndex: 1,
  position: "absolute",
  listStyle: "none",
  backgroundColor: theme.palette.background.paper,
  overflow: "auto",
  maxHeight: 200,
  border: "1px solid rgba(0,0,0,.25)",
  '& li[data-focus="true"]': {
    backgroundColor: "#4a8df6",
    color: "white",
    cursor: "pointer",
  },
  "& li:active": {
    backgroundColor: "#2977f5",
    color: "white",
  },
}));

const Label = styled("label")({
  display: "block",
});

const Input = styled("input")(({ theme }) => ({
  width: 200,
  backgroundColor: theme.palette.background.paper,
  color: theme.palette.getContrastText(theme.palette.background.paper),
}));

export const SearchModal = ({ handleClose }) => {
  const classes = useStyles();

  const {
    getRootProps,
    getInputLabelProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    id: "use-autocomplete-demo",
    options: FOOD_LIST,
    getOptionLabel: (option) => option.title,
  });

  return (
    <Box className={classes.paper}>
      <div className="search-bar">
        <div className="search-bar__head" {...getRootProps()}>
          {/* <input
            type="text"
            placeholder="Vyhledej mě..."
            class="search-bar__input"
          ></input> */}

          <Input
            placeholder="Vyhledej mě..."
            className="search-bar__input"
            {...getInputProps()}
          />
          {window.screen.width < 450 ? (
            <Tooltip title="Zavřít Menu">
              <IconButton
                onClick={() => {
                  handleClose();
                }}
                aria-label="Close Menu"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
          ) : (
            ""
          )}
        </div>
        <div className="search-bar__body">
          {console.log("groupedOptions", groupedOptions)}
          {groupedOptions.length > 0 ? (
            <div className="search-bar__list" {...getListboxProps()}>
              {groupedOptions.map((option, index) => (
                <li
                  className="search-card"
                  {...getOptionProps({ option, index })}
                  blurOnSelect={false}
                >
                  <div className="search-card__head">
                    <img src={option.imgSrc} alt={option.title} />
                  </div>
                  <div className="search-card__body">{option.title}</div>
                </li>
              ))}
            </div>
          ) : (
            <h3>Nic Jsme Nenašli</h3>
          )}
        </div>
        <div className="search-bar__foot"></div>
      </div>
    </Box>
  );
};
