import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from "@material-ui/core";
import { Fabs } from "../compontents/Fabs";
import { useFoodStateValue } from "../context/FoodStateContext";
import { useResponseStateValue } from "../context/ResponseStateContext";
import { useAuthValue } from "../context/AuthContext";
import { useRecipesValue } from "../context/RecipesContext";
import { firebase } from "../firebase";

const useStyles = makeStyles((theme) => ({
  width: {
    minWidth: "250px",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export function UpdateRecipeModal({ fab, value, index, icon }) {
  const [open, setOpen] = useState(false);
  const { foodState, setFoodState, updateFoodId, setUpdateFoodId } =
    useFoodStateValue();
  const [inputText, setInputText] = useState(updateFoodId.name);
  const { recipes, setRecipes } = useRecipesValue();
  const { currentUser } = useAuthValue();
  const { setResponse, isLoading, setIsLoading } = useResponseStateValue();
  const classes = useStyles();

  const updateRecipe = async () => {
    setOpen(false);
    setIsLoading(true);
    try {
      await firebase
        .firestore()
        .collection("recipes")
        .doc(updateFoodId.docId)
        .update({
          name: inputText,
          itemsList: foodState,
          userId: currentUser && currentUser.uid,
        });

      setUpdateFoodId();
      setRecipes([...recipes]);
      setFoodState([]);
      setResponse({
        active: true,
        message: "Recept byl úspěšně upraven",
        type: "success",
      });
    } catch (error) {
      setResponse({
        active: true,
        message: error.message,
        type: "error",
      });
    }
    setIsLoading(false);
  };

  return (
    <div>
      <Fabs
        fab={fab}
        value={value}
        index={index}
        onClick={() => setOpen(true)}
        isLoading={isLoading}
        icon={icon}
        isAddFood
        tooltipText="Upravit Recept"
      />
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle>Upravit Recept</DialogTitle>
        <DialogContent className={classes.width}>
          <TextField
            onInput={(e) => setInputText(e.target.value)}
            autoFocus
            margin="dense"
            id="name"
            label="Název Receptu"
            value={inputText}
            type="email"
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button
            disabled={isLoading}
            onClick={() => setOpen(false)}
            color="primary"
          >
            Zrušit
          </Button>
          <Button
            disabled={inputText === "" || isLoading}
            onClick={updateRecipe}
            color="primary"
          >
            Upravit
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
