import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(0.5),
    },
    whiteSpace: "nowrap",
    height: "40px",
    display: "flex",
    overflowY: "scroll",
  },
}));

export const FoodItemFilter = ({ filterOption, setFilterOption }) => {
  const classes = useStyles();

  return (
    <div className={classes.chipsContainer} style={{ marginTop: "10px" }}>
      <div className={classes.root}>
        <Chip
          className={`chip ${
            filterOption === "all-food-items" ? "chip--active" : ""
          }`}
          label="Všechny Potraviny"
          onClick={() => setFilterOption("all-food-items")}
          clickable
          color="primary"
          variant={filterOption === "all-food-items" ? undefined : "outlined"}
        />
        <Chip
          className={`chip ${
            filterOption === "recommended" ? "chip--active" : ""
          }`}
          label="Doporučené"
          onClick={() => setFilterOption("recommended")}
          clickable
          color="primary"
          variant={filterOption === "recommended" ? undefined : "outlined"}
        />
      </div>
    </div>
  );
};
