import React, { useState, useEffect } from "react";
import { AppBar, Tabs, Tab, Container, Tooltip } from "@material-ui/core";
import { RecipeFoodPage, RecipeItemsPage, RecipeNutrition } from "..";
import { TabPanel, CreateRecipeModal, Fabs } from "../../compontents";
import { UpdateRecipeModal } from "../../compontents/UpdateRecipeModal";
import { a11yProps } from "../../helpers";
import { useFoodStateValue } from "../../context/FoodStateContext";
import { FABS_ADD, FABS_UPDATE, FABS_EXIT } from "../../constants/fabs";
import { useHistory } from "react-router-dom";

import { useUserDataValue } from "../../context/UserDataContext";
import { useModesValue } from "../../context/ModesContext";

import AddIcon from "@material-ui/icons/Add";
import KeyboardReturnIcon from "@material-ui/icons/KeyboardReturn";
import EditIcon from "@material-ui/icons/Edit";
import { getFoodList } from "../../constants/mock-food";

export function RecipePage({ header }) {
  const [value, setValue] = useState(0);
  const [isPassedHeader, setIsPassedHeader] = useState(false);
  const { foodState, setFoodState, updateFoodId, setUpdateFoodId } =
    useFoodStateValue();
  const { showAllFood, setShowAllFood } = useModesValue();
  const [filterOption, setFilterOption] = useState("all-food-items");

  const { userData, setUserData } = useUserDataValue();

  const handleHeaderScroll = () => {
    const scrollDistance = window.scrollY;
    const headerHeight = header.current.offsetHeight;

    scrollDistance >= headerHeight
      ? setIsPassedHeader(true)
      : setIsPassedHeader(false);
  };

  const history = useHistory();

  useEffect(() => {
    //! isPassedHeader
    // window.addEventListener("scroll", handleHeaderScroll);
    // console.log('updateFoodState', updateFoodId);
    console.log("foodState", foodState);

    getFoodList(showAllFood);
    // console.log(userData && userData);
    return () => {
      window.removeEventListener("scroll", handleHeaderScroll);
    };
  }, [showAllFood, foodState]);

  return (
    <Container className="create-recipe">
      <div className="create-recipe__header">
        <AppBar position="static" color="default">
          <Tabs
            value={value}
            onChange={(e, index) => setValue(index)}
            indicatorColor="primary"
            textColor="primary"
            variant="fullWidth"
            aria-label="action tabs example"
          >
            <Tab label="Potraviny" {...a11yProps(0)} />
            <Tab label="Recept" {...a11yProps(1)} />
            <Tab label="Živiny" {...a11yProps(2)} />
          </Tabs>
        </AppBar>
      </div>
      <div className="create-recipe__content" onScroll={handleHeaderScroll}>
        <TabPanel value={value} index={0}>
          <RecipeFoodPage
            showAllFood={showAllFood}
            setShowAllFood={setShowAllFood}
            filterOption={filterOption}
            setFilterOption={setFilterOption}
          />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <RecipeItemsPage />
        </TabPanel>
        <TabPanel value={value} index={2}>
          <RecipeNutrition foodState={foodState} setFoodState={setFoodState} />
        </TabPanel>
      </div>
      {!updateFoodId &&
        FABS_ADD.map((fab, index) => (
          <CreateRecipeModal
            key={index}
            fab={fab}
            value={value}
            index={index}
            icon={<AddIcon className="fab__icon" />}
          />
        ))}

      {updateFoodId &&
        FABS_UPDATE.map((fab, index) => (
          <Tooltip title="Upravit Recept">
            <UpdateRecipeModal
              key={index}
              fab={fab}
              value={value}
              index={index}
              icon={<EditIcon className="fab__icon" />}
            />
          </Tooltip>
        ))}
      {updateFoodId &&
        FABS_EXIT.map((fab, index) => (
          <Fabs
            onClick={() => {
              setFoodState([]);
              setUpdateFoodId();
            }}
            key={index}
            fab={fab}
            value={value}
            index={index}
            icon={<KeyboardReturnIcon className="fab__icon" />}
            isSecondary
            tooltipText="Zrušit Editování"
          />
        ))}
    </Container>
  );
}
