import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  CardActions,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  Divider,
  Modal,
  Box,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { useManageFoodState, useFoodStateHelpers } from "../hooks";
import { ALL_FOOD_LIST } from "../constants/mock-food";

const useStyles = makeStyles((theme) => ({
  checkMark: {
    width: "1.3rem",
    height: "1.3rem",
    color: "#1976d2",
    marginLeft: "1rem",
  },
  fitImage: {
    objectFit: "contain",
  },
  paper: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "80%",
    height: "70%",
    backgroundColor: "white",
    border: "2px solid #e2e2e2",
    boxShadow: 24,
    padding: 20,
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "flex-start",
    alignItems: "flex-start",
    overflow: "scroll",
    outline: "none",
  },
}));

export const RecipeFoodCard = ({
  foodId,
  title,
  imgSrc,
  nutrition,
  grammageInfo,
  price,
  variants,
  category,
  nutrientValue,
  nutrientUnit,
  small,
  currentVariant,
  food,
  isMacro,
  nutrientType,
  nutrientName,
}) => {
  const { isFoodAvailable } = useFoodStateHelpers();
  const { addFoodToState, removeFoodFromState } = useManageFoodState();

  const [included, setIncluded] = useState(false);
  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handeFoodInclude = () => {
    if (variants && variants.length > 0) {
      handleOpen();
    } else {
      if (!included) {
        setIncluded(true);
        addFoodToState(
          title,
          imgSrc,
          nutrition,
          foodId,
          grammageInfo,
          price,
          variants,
          category,
          currentVariant
        );
      } else {
        setIncluded(false);
        removeFoodFromState(foodId);
      }
    }
  };

  useEffect(() => {
    isFoodAvailable(foodId) ? setIncluded(true) : setIncluded(false);
  }, [isFoodAvailable, foodId]);

  //! -------Start

  const getSubFoodItems = () => {
    const filteredItems = ALL_FOOD_LIST.filter(
      (currentFood) => currentFood.title === title
    );

    return filteredItems.map((item) => {
      const {
        id,
        title,
        imgSrc,
        nutrition,
        grammageInfo,
        price,
        category,
        currentVariant,
      } = item;

      // console.log(
      //   food.variants.findIndex((variant) => variant === currentVariant)
      // );

      // console.log(
      //   "nutrientType",
      //   foodId,
      //   title,
      //   imgSrc,
      //   nutrition,
      //   grammageInfo,
      //   price,
      //   variants,
      //   category,
      //   nutrientValue,
      //   nutrientUnit,
      //   small,
      //   currentVariant,
      //   food,
      //   isMacro,
      //   nutrientType,
      //   nutrientName
      // );

      let nutritionValue1;

      if (
        food.variants &&
        food.variants.length > 0 &&
        nutrientName &&
        nutrientType &&
        isMacro
      ) {
        const variantIndex = food.variants.findIndex(
          (variant) => variant === currentVariant
        );

        console.log(isMacro, isMacro ? "total" : nutrientName, nutrientType);
        console.log(variants, variantIndex);
        console.log(food[`nutrition${variantIndex === 0 ? "" : variantIndex}`]);

        // console.log(
        //   variantIndex >= 0 ? variantIndex : "",
        //   food[`nutrition${variantIndex >= 0 ? variantIndex : ""}`]
        // );

        nutritionValue1 =
          food[`nutrition${variantIndex === 0 ? "" : variantIndex}`][
            nutrientType
          ][isMacro ? "total" : nutrientName];
      }

      return (
        <RecipeFoodCard
          foodId={id}
          title={title}
          imgSrc={imgSrc}
          nutrition={nutrition}
          grammageInfo={grammageInfo}
          price={price}
          currentVariant={currentVariant}
          category={category}
          nutrientValue={nutritionValue1}
          nutrientType={nutrientType}
          nutrientName={nutrientName}
          nutrientUnit={nutrientUnit}
          food={food}
          isMacro={isMacro}
        />
      );
    });
  };

  //! ------End

  const loweredCamelTitle = title.replace(/\s/g, "-").toLowerCase();

  return (
    <>
      <Card
        className={`food-card ${
          small ? "food-card--small" : ""
        } food-card--${loweredCamelTitle}`}
      >
        <CardActionArea onClick={() => handeFoodInclude()}>
          <CardMedia
            component="img"
            alt="Contemplative Reptile"
            height="140"
            image={imgSrc}
            title={title}
            className={classes.fitImage}
          />
          <CardContent className="food-card__content">
            <div style={{ width: "100%" }}>
              <Typography variant="h6" component="h3">
                {title}
              </Typography>
              <Typography variant="h6" component="h3" color="textSecondary">
                {currentVariant && <div>{currentVariant}</div>}
                <Divider style={{ margin: "12px 0 10px 0" }} />
                {nutrientValue && (
                  <div>
                    {nutrientValue} {nutrientUnit}
                  </div>
                )}
              </Typography>
            </div>
            {included && <CheckCircleIcon className={classes.checkMark} />}
          </CardContent>
        </CardActionArea>
        <CardActions
          style={{
            padding: "10px 16px 16px 16px",
          }}
        >
          {/* <Button variant='outlined' size='small' color='primary'>
          Info
        </Button> */}
          <Button
            size="small"
            variant={included ? "contained" : "outlined"}
            // variant='contained'
            color="primary"
            onClick={() => handeFoodInclude()}
          >
            {included ? "Odebrat" : "Přidat"}
          </Button>

          <div>
            {variants && variants.length > 0 ? (
              <Typography variant="body1" component="h6">
                + {variants.length}
              </Typography>
            ) : (
              ""
            )}
          </div>
        </CardActions>
      </Card>

      <Modal open={open} onClose={handleClose}>
        <Box class={classes.paper}>{getSubFoodItems()}</Box>
      </Modal>
    </>
  );
};
