import React, { useEffect, useState } from "react";
import { firebase } from "../firebase";
import { useHistory } from "react-router-dom";
import { MenuItemCardNutrtients } from "../compontents/MenuItemCardNutrtients";
import {
  Card,
  CardContent,
  Avatar,
  ListItemText,
  MenuItem,
  Menu,
  IconButton,
  Tooltip,
} from "@material-ui/core";

import { useRecipesValue } from "../context/RecipesContext";
import { useResponseStateValue } from "../context/ResponseStateContext";
import { useFoodStateValue } from "../context/FoodStateContext";
import { useMenuStateValue } from "../context/MenuStateContext";
import { createNewKey } from "../helpers/createNewKey";

import MoreVertIcon from "@material-ui/icons/MoreVert";
import FastfoodIcon from "@material-ui/icons/Fastfood";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import LibraryAddIcon from "@material-ui/icons/LibraryAdd";
import EditIcon from "@material-ui/icons/Edit";

import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  details: {
    display: "flex",
    width: "100%",
    alignItems: "center",
    justifyContent: "space-between",
    borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
  },
  content: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    paddingBottom: "16px !important",
  },
  cover: {
    width: 45,
    height: 45,
    backgroundSize: "contain",
    marginRight: "10px",
    color: "#43a047",
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  marginRight: {
    marginRight: 10,
  },
  primaryColor: {
    color: "rgba(0, 0, 0, 0.60)",
  },
}));

const ITEM_HEIGHT = 48;

export const MenuItemCard = ({ recipe }) => {
  const { foodState, setFoodState, updateFoodId, setUpdateFoodId } =
    useFoodStateValue();
  const { menuState, setMenuState } = useMenuStateValue();
  const { recipes, setRecipes } = useRecipesValue(false);
  const { setResponse, setIsLoading } = useResponseStateValue();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const classes = useStyles();

  const history = useHistory();

  const { name, itemsList, docId } = recipe;

  const removeItemFromMenu = () => {
    const newMenuState = menuState.filter(({ foodName }) => foodName === name);
    setMenuState(newMenuState);
  };

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const addRecipeToMenu = (currentRecipe) => {
    console.log("currentRecipe", currentRecipe);
    setMenuState(currentRecipe);
    handleClose();
  };

  const deleteRecipe = async () => {
    setIsLoading(true);
    try {
      await firebase
        .firestore()
        .collection("recipes")
        .doc(docId)
        .delete()
        .then(() => {
          setResponse({
            active: true,
            message: `Recept ${name} byl úspěšně smazán`,
            type: "success",
          });
          setRecipes([...recipes]);
          removeItemFromMenu();
          handleClose();
        });
    } catch (error) {
      setResponse({
        active: true,
        message: error.message,
        type: "success",
      });
    }

    setIsLoading(false);
  };

  const redirectToUpdate = () => {
    setUpdateFoodId({ docId, name });
    setFoodState(itemsList);
    removeItemFromMenu();
    history.push("./create-recipe");
    handleClose();
  };

  return (
    <Card className="menu-card">
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Avatar color="primary" className={classes.marginRight}>
            <FastfoodIcon />
          </Avatar>
          <ListItemText primary={name} secondary="Jan 9, 2014" />
        </CardContent>
        <div className="menu-card__button">
          <Tooltip title="Možnosti">
            <IconButton
              aria-label="more"
              aria-controls="long-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              <MoreVertIcon />
            </IconButton>
          </Tooltip>
          <Menu
            id="long-menu"
            anchorEl={anchorEl}
            keepMounted
            open={open}
            onClose={handleClose}
            PaperProps={{
              style: {
                maxHeight: ITEM_HEIGHT * 4.5,
                display: "flex",
                justifyContent: "space-between",
              },
            }}
          >
            <MenuItem
              className={classes.primaryColor}
              onClick={() => {
                addRecipeToMenu([
                  ...menuState,
                  { ...recipe, id: createNewKey(5) },
                ]);
              }}
            >
              <LibraryAddIcon color="primary" className={classes.marginRight} />{" "}
              Přidat do Menu
            </MenuItem>
            <MenuItem
              className={classes.primaryColor}
              onClick={redirectToUpdate}
            >
              <EditIcon color="primary" className={classes.marginRight} />{" "}
              Upravit
            </MenuItem>
            <MenuItem className={classes.primaryColor} onClick={deleteRecipe}>
              <DeleteForeverIcon
                color="primary"
                className={classes.marginRight}
              />
              Odstranit
            </MenuItem>
          </Menu>
        </div>
      </div>
      <MenuItemCardNutrtients itemsList={itemsList} />
    </Card>
  );
};
