import React, { useState } from "react";
import {
  Radio,
  RadioGroup,
  FormControl,
  FormLabel,
  FormControlLabel,
  Button,
  TextField,
} from "@material-ui/core";

export const Step2 = ({
  age,
  setAge,
  height,
  setHeight,
  weight,
  setWeight,
  gender,
  setGender,
  handleStep,
}) => {
  const [value, setValue] = useState("a");

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  return (
    <>
      <TextField
        style={{ marginTop: "1rem" }}
        className="intro-page__input"
        id="standard-number1"
        label="Věk"
        type="number"
        value={age}
        onChange={(e) => setAge(e.target.value)}
        InputLabelProps={{
          shrink: true,
        }}
      />
      <TextField
        className="intro-page__input"
        id="standard-number2"
        label="Výška"
        placeholder="cm"
        type="number"
        value={height}
        onChange={(e) => setHeight(e.target.value)}
        InputLabelProps={{
          shrink: true,
        }}
      />
      <TextField
        className="intro-page__input"
        id="standard-number3"
        label="Váha"
        placeholder="kg"
        type="number"
        value={weight}
        onChange={(e) => setWeight(e.target.value)}
        InputLabelProps={{
          shrink: true,
        }}
      />
      <FormControl className="intro-page__input" component="fieldset">
        <FormLabel style={{ marginBottom: "0.6rem" }} component="legend">
          Pohlaví
        </FormLabel>
        <RadioGroup
          aria-label="gender"
          name="gender1"
          value={gender}
          onChange={(e) => setGender(e.target.value)}
        >
          <FormControlLabel
            value="male"
            control={<Radio color="primary" />}
            label="Muž"
          />
          <FormControlLabel
            value="female"
            control={<Radio color="primary" />}
            label="Žena"
          />
        </RadioGroup>
      </FormControl>
      <div>
        <Button onClick={() => handleStep(0)}>Zpět</Button>
        <Button
          variant="contained"
          color="primary"
          disabled={!age || !weight || !height || !gender}
          onClick={() => handleStep(2)}
        >
          Pokračovat
        </Button>
      </div>
    </>
  );
};
