import React, { useState, useEffect } from "react";
import Chip from "@material-ui/core/Chip";
import { useFoodStateValue } from "../context/FoodStateContext";
import { useManageFoodState, useFoodStateHelpers } from "../hooks";

export const Chiper = ({ seasoning, type }) => {
  const [selected, setSelected] = useState(false);
  const {
    title,
    imgSrc,
    nutrition,
    id,
    grammageInfo,
    price,
    variants,
    category,
  } = seasoning;

  const { foodState, setFoodState } = useFoodStateValue();
  const { isFoodAvailable } = useFoodStateHelpers(title);
  const { addFoodToState, removeFoodFromState } = useManageFoodState();

  const handleClick = () => {
    console.log("title", title, grammageInfo);

    if (selected) {
      setSelected(false);
      removeFoodFromState(title);
    } else {
      setSelected(true);
      addFoodToState(
        title,
        imgSrc,
        nutrition,
        id,
        grammageInfo,
        price,
        variants,
        category
      );
    }
  };

  useEffect(() => {
    isFoodAvailable(title) ? setSelected(true) : setSelected(false);
  }, [isFoodAvailable, title]);

  return (
    <Chip
      className={`chip chip--${type} ${selected ? "chip--active" : ""}`}
      label={title}
      onClick={handleClick}
      clickable
      variant={!selected ? "outlined" : undefined}
      color="primary"
    />
  );
};
